.cityBox{
  height:100%
}
.citylist {
  height: calc(100% - 45px);
}
// 城市列表
.city-item {
  background: #fff;
  .title {
      font-size: 14px;
      padding: 10px 15px;
      color: #999;
  }
  .name {
      width: 100%;
      height: 50px;
      padding: 0 15px;
      line-height: 50px;
      border-bottom: 1px solid #eeeeee;
      font-size: 16px;
      color: #333;
      cursor: pointer;
  }
}
 // 右侧索引
 .city-index {
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 5px;
  z-index: 1;
  height: 90%;
  box-sizing: border-box;
  padding-top: 20px;
  text-align: center;

  .city-index-item {
    flex: 1;
  }

  .index-active {
    color: #fff;
    background-color: #21b97a;
    border-radius: 100%;
    display: inline-block;
    font-size: 12px;
    width: 15px;
    height: 15px;
    line-height: 15px;
  }
}